<template>
    <div class="card-part" :style="partStyle">
        <div class="card-img" :class="{'on':card.status === 1||card.status === 2}">
            <img v-lazy="card.img+'?imageMogr2/thumbnail/!50p'" alt=""/>
            <icon-font name="ArtboardCopyx2" size="116" color="#3CB963" class="icon-tag" v-if="card.status===2"/>
            <icon-font name="ArtboardCopyx1" size="116" color="#858585" class="icon-tag" v-if="card.status===3"/>
            <icon-font name="ArtboardCopyx" size="116" color="#858585" class="icon-tag" v-if="card.status===5"/>
            <p class="days"><span>{{card.days}}</span>天</p>
        </div>
        <div class="card-info">
            <div class="info-title">{{card.name}}</div>
            <div class="info-desc">{{card.days}}天绿卡体验特权</div>
            <div class="info-time">{{card.expire_time}} 前可赠送</div>
            <div class="info-btn" v-if="card.status === 1||card.status === 2" @click="_eventCard">{{card.status===2?'再次赠送':'去赠送'}}</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['card','margin'],
        computed:{
            partStyle(){
                let style = {};
                if(this.margin){
                    style.marginBottom = `${this.margin}px`;
                }
                return style;
            }
        },
        methods: {
            _eventCard() {
                this.$emit('clickSend',this.card)
            }
        }
    }
</script>

