<template>
    <div class="vip-friends">
        <div class="friends-banner">
            <div class="info-enter" @click="eventUseState">使用说明</div>
            <img v-lazy="index_banner" alt=""/>
        </div>

        <div class="friends-content">
            <friends-card v-for="(card, c) in list" :card="card" :key="c" margin="15" @clickSend="eventToShare"/>
        </div>
    </div>
</template>

<script>
    import API from '@/server/api'
    import share from '@/mixins/share'
    import FriendsCard from '@/components/vip/FriendsCard'
    import {pageUrl} from '@/utils/tools'
    import {URL_INFO_SINGLE} from '@/const/urls'

    export default {
        name: "vip-friends",
        components: {
            FriendsCard
        },
        mixins: [share],
        data() {
            return {
                invite_code: '', //小哥推广码
                share_info: null,
                index_banner: '',
                list: ''
            };
        },
        mounted() {
            let {invite_code} = this.$route.query;
            this.invite_code = invite_code || '';
            this.requestFriends();
            this.log({
                cid: 'friends_vip_card',
                aid: 'index'
            });
        },
        methods: {
            async requestFriends(loading = true) {
                const {data} = await this.$http.get(API.vipApi('friendsCardList'), {}, loading);
                if (data) {
                    const {share_info, index_banner, list} = data;
                    this.share_info = share_info;
                    this.index_banner = index_banner;
                    this.list = list;
                    this.updateShare(share_info);
                }
            },

            async requestFriendsSend(id) {
                const {data} = await this.$http.get(API.vipApi('friendsCardSend'), {card_id: id}, false);
                if (data) {
                    this.requestFriends(false);
                }
            },

            eventUseState() {
                this.openUrl({path: URL_INFO_SINGLE, query: {alias:'friend_card'}});
            },

            eventToShare(card) {
                const {id, status} = card;
                if (status === 1) {
                    this.requestFriendsSend(id);
                }
                this.log({
                    cid: 'friends_vip_card',
                    aid: status === 1 ? 'share' : 'reshare',
                    name: id
                });
                this.updateShare({link: pageUrl('/vip/friendsGive', {card_id: id})});
                this.appShare();
            }
        }
    }
</script>

<style lang="scss" scoped>
    .friends-banner {
        position: relative;
        width: 100%;
        height: 430px;
        background: #e2e2e2;
        img {
            width: 100%;
            height: 100%;
        }
        .info-enter {
            position: absolute;
            right: 0;
            top: 20px;
            width: 115px;
            height: 46px;
            padding-left: 6px;
            line-height: 47px;
            text-align: center;
            background: rgba(0, 0, 0, .5);
            border-radius: 28px 0 0 28px;
            font-size: 22px;
            color: #FFFFFF;
        }
    }

    .friends-content {
        padding: 30px 30px 0 30px;
        box-sizing: border-box;
    }
</style>